.menu_side_panel {
  text-align: center;
  width: 88px;
  float: left;
  padding-top: 5vh;
  height: 100vh;
  z-index: 10;
  background-color: #fff;
  position: relative;
  margin-right: 4px;
}

.enquiry_page .menu_side_panel {
  /* height: 100vh; */
}
#menu_icon {
  display: inline-block;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
}
.menu-bar {
  height: 3px;
  margin: 0 0 3px 20px;
  opacity: 0.7;
  border-radius: 1.5px;
  background-color: #24272c;
  display: block;
  clear: both;
}
.menu-bar-1 {
  width: 23px;
}
.menu-bar-2 {
  width: 19px;
}
.menu-bar-3 {
  width: 15px;
}
#menu_icon label {
  margin: 5px 0 0;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #24272c;
  text-transform: capitalize;
}
#menu_icon label svg {
  width: 13px;
  margin-right: 5px;
  vertical-align: middle;
}

.nav {
  margin-top: 23px;
  margin-left: 13%;
}
.nav ul li a img {
  width: 25px;
}
.nav ul li a {
  font-family: Roboto;
  font-size: 12px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #24272c;
  text-decoration: none;
  display: inline-block;
  padding: 6px 12px;
  border-radius: 12px;
}
.nav ul li {
  margin: 5px 0;
}
.nav ul li a span {
  clear: both;
  display: block;
  margin: 3px 0 0;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #24272c;
}
.nav ul li:hover {
  background: rgba(50, 197, 255, 0.1);
  border-radius: 8px;
}

.nav ul li a.active span,
.nav ul li a:hover span {
  font-weight: 500;
  color: #12b0ee;
}
.left_btm_logo {
  position: absolute;
  bottom: 31px;
  left: 27px;
}
#menu_icon.active {
  background: rgba(50, 197, 255, 0.1);
}
#menu_icon.active label {
  color: #12b0ee;
}
#menu_icon.active .menu-bar {
  background-color: #12b0ee;
}

.switch_busienss .MuiInput-root {
  width: 100% !important;
}
