a {
  text-decoration: none;
}
.hide {
  display: none;
}
ul li {
  list-style-type: none;
}
.text-center {
  text-align: center;
}
.top_logo {
  width: 200px;
}
#header_inner.header-sticked {
  position: fixed;
  width: 100%;
  background: #fff;
  z-index: 4;
  height: 87px;
}
.btm_whatsapp {
  margin-top: 10px;
}
#footer_social .elementor-social-icon {
  background: 0 0;
}
#footer_social li {
  list-style-type: none;
  float: left;
}
#footer_link a {
  font-weight: 700;
  color: #000;
}
.footer_logo {
  width: 193px;
}
.whats_demo_block span {
  font-size: 117px;
  color: #cdd7e1;
  float: left;
  font-weight: 700;
}
.whats_demo_block {
  position: relative;
}
.whats_demo_block .content_div {
  background: #eff3f6;
  min-height: 304px;
  border-radius: 35px;
  padding: 0 30px 10px;
}
.whats_demo_block .content_div h2 {
  float: left;
  padding-top: 58px;
  display: block;
  clear: both;
  font-size: 25px;
}
.whats_demo_block .content_div p {
  display: block;
  text-align: left;
  width: 210px;
  clear: both;
}
@media (min-width: 768px) {
  .whats_demo_block .elementor-image {
    position: absolute;
    right: 12px;
    top: -86px;
  }
}
#richwhatsappPanel {
  padding-top: 113px;
}
.manyMore {
  margin-top: 40px;
  font-size: 50px !important;
  color: #ced6e1 !important;
  display: block;
  width: 100%;
  clear: both;
  text-align: center;
}
@media (max-width: 767px) {
  #header_inner {
    width: 100%;
    position: fixed;
    height: 75px;
    background: #12b0ee;
    z-index: 1111111111111111;
  }
  .whats_demo_block .elementor-image {
    position: relative;
    margin-top: 20px;
  }
  .whats_demo_block .content_div p {
    width: 100%;
  }
  .manyMore {
    margin-top: -35px;
  }
  #richwhatsappPanel {
    padding-top: 23px;
  }
  #footer_logo_image {
    width: 100%;
    text-align: center;
  }
  #footer_social li {
    display: contents;
  }
}
.floatbtn,
.floatbtn:hover {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  right: 30px;
  background-color: #0c9;
  color: #fff;
  border-radius: 50px;
  text-align: center;
  box-shadow: 2px 2px 3px #999;
  border: 1px solid #0c9;
}
.my-floatbtn {
  margin-top: 14px;
}
.accordion__button:before {
  float: right !important;
}
.accordion .accordion__button {
  font-family: Roboto;
  font-size: 13px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  color: #24272c;
  background: 0 0;
}
.accordion {
  border: none !important;
}
.accordion .accordion__panel {
  padding: 0 !important;
}
.accordion__button:before {
  transform: rotate(45deg) !important;
}
.accordion__button[aria-expanded="true"]::before,
.accordion__button[aria-selected="true"]::before {
  transform: rotate(-136deg) !important;
  margin-top: 5px !important;
}
.accordion__button:focus,
.accordion__button:hover {
  background-color: transparent !important;
  border: none;
  outline: 0;
}
#background_layer {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  height: 100vh;
  width: 100vw;
  z-index: 1;
  top: 0;
  left: 100px;
}
.business_region_list {
  clear: both;
  margin-top: 12px;
}
.business_region_list .Dropdown-control {
  border: none;
  padding: 0 24px 0 0;
  font-size: 14px;
  color: #24272c;
  background: 0 0;
}
.business_region_list .Dropdown-arrow {
  top: 7px;
  border-color: #24272c transparent transparent;
}
.business_region_list .Dropdown-placeholder {
  border-bottom: solid 1px #000;
  min-width: 71px;
}
.business_region_list .Dropdown-option {
  clear: both;
}
.business_region_list .Dropdown-menu {
  border-left: 0;
  border-right: 0;
}
